<template>
  <div class='right_frame'>
    <h3> </h3>
    <div class='info'>
      <span>// </span>
      <router-link :to='modfiyPasswordPath'></router-link>
    </div>
    <div>
      <table class="table2" width="100%">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th>IP</th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for='info in modifyInfos' :key='info.date'>
            <td>{{info.date}}</td>
            <td>{{info.time}}</td>
            <td>{{info.ip}}</td>
            <td> {{info.city}}</td>
            <td>{{info.operate}}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
import Paths from "@/config/path.js";

export default {
  data() {
    return {
      modfiyPasswordPath: Paths.userSecuritys.userPassword,
      user: null,
      util: this.$root,
      modifyInfos: []
    };
  },
  methods: {
    initData: function(data) {
      for (var i in data) {
        let d = new Date(data[i].sensitiveOperationPK.time);
        let date =
          d.getFullYear() + "" +(d.getMonth()+1)  + "" + d.getDate() + "";
        let time = d.getHours() + ":" + d.getMinutes() + ":" + d.getSeconds();
        let p = {
          date: date,
          time: time,
          ip: data[i].ip,
          city: data[i].city,
          operate: data[i].operation
        };
        this.modifyInfos.push(p);
      }
    },
    getData: function() {
      this.user = this.util.getUser();

      let that = this;
      let url = this.$config.dataURL + this.$URL.person.sensitiveOperation;
      this.util.$post(url, {}, function(res) {
        let data = res.data;
        if (data.status) {
          that.initData(data.operate);
        } else {
        }
      });
    }
  },
  created() {
    this.getData();
  }
};
</script>
<style lang="scss" scoped>
@import "./default.scss";

.right_frame > h3 {
  font-size: 1.4em;
  font-weight: bold;
  color: #666;
}
.info {
  line-height: 1.5em;
}
</style>



