    <div class='info'>
      <div></div>
      <div>?</div>
      <div></div>
    </div>
    <div class='bdf'>
      <h3></h3>
      <div></div>
      <div>{{dataInfo.name}}<span :tel='dataInfo.phone'>{{dataInfo.phone}}</span></div>
    </div>
  </div>
</template>
<script>
import Paths from "@/config/path.js";

export default {
  data() {
    return {
      modfiyPasswordPath: Paths.userSecuritys.userPassword,
      user: null,
      util: this.$root,
      dataInfo:{
        name:'NTD Phone',
        phone:'020-00000000'
      }
    };
  },
  methods: {
    upload: function() {
      alert("upload");
    }
  },
  created() {
    this.user = this.util.getUser();
  }
};
</script>
<style lang="scss" scoped>
@import "./default.scss";


</style>



