<template>
  <div class='right_frame'>
    <h3></h3>
    <div class='email'>
      <span class='green'>{{getUser().email}}</span>
      <a @click="modify"></a>
      <email-box v-if='emailBoxShow' v-model="emailBoxShow"></email-box>
    </div>
    <div class='info'>NTD Phone</div>
    <div class='bdf'>
      <h3></h3>
      <ul>
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </div>
  </div>
</template>
<script>
import EmailBox from "@/components/user/security/EmailBox.vue";

export default {
  components: {
    EmailBox
  },
  data() {
    return {
      util: this.$root,
      emailBoxShow:false
    };
  },
  methods: {
    modify:function(){
      this.emailBoxShow=true;
    },
    getUser: function() {
      return this.util.getUser();
    },
  },
  created() {
    //this.emailBoxShow=true;
    this.user = this.util.getUser();
  }
};
</script>
<style lang="scss" scoped>
@import "./default.scss";

.info {
  padding-bottom: 2em;
}
.green {
  color: #4bba1e;
  font-size: 0.9em;
  margin-right: 15px;
  word-wrap: break-word;
}
.email {
  padding: 10px 0px;
}
@media (min-width: 600px) {
  .green {
    font-size: 1.5em;
  }
}
ul {
  margin-left: 1.5em;
}
li {
  cursor: pointer;
  margin-top: 1em;
}
</style>



