<template>
  <div class='right_frame'>
    <h3></h3>
    <div></div>
    <password-box v-if='passwordBoxShow' v-model='passwordBoxShow'></password-box>
    <button class='cbtn' @click='modify'></button>
    <div class='bdf'>
      <h3></h3>
      <p>
        <a></a></p>
    </div>
  </div>
</template>
<script>
import PasswordBox from "@/components/user/security/PasswordBox.vue";

export default {
  components: {
    PasswordBox
  },
  data() {
    return { passwordBoxShow: false };
  },
  methods: {
    modify:function () {
      this.passwordBoxShow = true;
    }
  },
  created() {
    //this.passwordBoxShow = true;
  }
};
</script>
<style lang="scss" scoped>
@import "./default.scss";
</style>



